.hero{
    height: 100vh;
    background:linear-gradient(180deg, rgb(113, 171, 182),#e1ffea22 60%) ;
    display: flex;

}
.hero-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding-left: 180px;
    line-height: 1.1;

}
.hero-left h2{
    color: #090909;
    font-size: 30px;
    font-weight: 600;
    padding-top: 150px;

}
.hero-left p{
    color: #171717;
    font-size: 100px;
    font-weight: 700;
    margin-top: 0%;


}
.hero-hand-icon{
    display: flex;
    align-items: center;
    gap: 20px;

}
.hero-hand-icon img{
    width: 105px;

}
.hero-latest-btn
{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background-color: #ff4141;
    color: white;
    font-size: 22px;
    font-weight: 500;
}
.hero-right
{

    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
